import React, { useEffect, useRef, useState } from 'react';
import { useAuth } from '../context/AuthContext';
import io from 'socket.io-client';
import Button from './Button';
import moment from 'moment-timezone';

const socket = io('http://localhost:5000', { autoConnect: false });

export default function ChatBox() {
  const { user, token } = useAuth();
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [open, setOpen] = useState(false);
  const messagesEndRef = useRef(null);

  useEffect(() => {
    if (!user || !open) return;
    socket.connect();
    socket.emit('join-client', user.id);
    fetchMessages();

    socket.on('new-message', msg => {
      setMessages(m => [...m, msg]);
    });

    return () => {
      socket.off('new-message');
      socket.disconnect();
    };
    // eslint-disable-next-line
  }, [user, open]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const fetchMessages = async () => {
    const res = await fetch('/api/chat/messages', {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    });
    const data = await res.json();
    if (data.success) setMessages(data.data);
  };

  const sendMessage = async e => {
    e.preventDefault();
    if (!input.trim()) return;

    // Save to DB
    await fetch('/api/chat/messages', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ message: input })
    });

    // Emit for real-time
    socket.emit('send-message', {
      message: input,
      sender_id: user.id,
      sender_name: user.username,
      sender_role: user.role,
      created_at: new Date().toISOString()
    });

    setInput('');
  };

  // Show a small open button when chat is closed
  if (!open) {
    return (
      <button
        onClick={() => setOpen(true)}
        style={{
          position: 'fixed',
          bottom: 32,
          right: 32,
          width: 64,
          height: 64,
          borderRadius: 12,
          background: '#2563eb',
          color: 'white',
          border: 'none',
          boxShadow: '0 2px 8px rgba(0,0,0,0.15)',
          zIndex: 1001,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          fontSize: 20,
          fontWeight: 600,
          cursor: 'pointer',
          letterSpacing: 1,
        }}
        aria-label="Open chat"
      >
        Chat
      </button>
    );
  }

  // Show the chatbox when open
  return (
    <div
      style={{
        position: 'fixed',
        bottom: 32,
        right: 32,
        width: 350,
        maxWidth: '90vw',
        height: 420,
        background: 'white',
        borderRadius: 16,
        boxShadow: '0 2px 16px rgba(0,0,0,0.18)',
        display: 'flex',
        flexDirection: 'column',
        zIndex: 1001,
      }}
    >
      <div style={{ display: 'flex', alignItems: 'center', padding: 12, borderBottom: '1px solid #eee', background: '#2563eb', color: 'white', borderTopLeftRadius: 16, borderTopRightRadius: 16 }}>
        <span style={{ flex: 1, fontWeight: 600 }}>Chat with Admin</span>
        <button
          onClick={() => setOpen(false)}
          style={{ background: 'none', border: 'none', color: 'white', fontSize: 20, cursor: 'pointer', marginLeft: 8 }}
          aria-label="Close chat"
        >
          ×
        </button>
      </div>
      <div style={{ flex: 1, overflowY: 'auto', padding: 12, background: '#f8fafc' }}>
        {messages.map((msg, i) => (
          <div key={i} style={{ marginBottom: 8 }}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', marginBottom: 4 }}>
              <b>{msg.sender_name}:</b>
              <small style={{ color: '#666', fontSize: '11px' }}>
                {msg.formattedTime || (msg.created_at ? moment(msg.created_at).tz('Asia/Kolkata').format('DD/MM/YYYY h:mm A') : '')}
              </small>
            </div>
            <div>{msg.message}</div>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>
      <form onSubmit={sendMessage} style={{ display: 'flex', padding: 12, borderTop: '1px solid #eee', background: '#f1f5f9', borderBottomLeftRadius: 16, borderBottomRightRadius: 16 }}>
        <input
          value={input}
          onChange={e => setInput(e.target.value)}
          style={{ flex: 1, borderRadius: 8, border: '1px solid #ddd', padding: 8, marginRight: 8 }}
          placeholder="Type your message..."
        />
        <Button type="submit">Send</Button>
      </form>
    </div>
  );
} 